import React from 'react';
import { Section, SectionTitle } from "style/common";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/swiper.scss";

const iconCss3 = "/images/icon_css3.png";

const Skills =  ()  => {
  return (
    <Section className="skills">
      <SectionTitle className="skills">Skills</SectionTitle>
      <Swiper>
        <SwiperSlide>
          <img src={iconCss3} alt=""/>
        </SwiperSlide>
        <SwiperSlide>22</SwiperSlide>
        <SwiperSlide>33</SwiperSlide>
        <SwiperSlide>44</SwiperSlide>
        <SwiperSlide>66</SwiperSlide>
      </Swiper>
    </Section>
  );
};

export default Skills;